.parent{
    background: #fff;
    display: flex;
    /* justify-content: space-evenly; */
    justify-content: space-between;
    border: 1px solid #c7c2c22e;
    align-items: center;
}

.dashboardOuter{
    padding: 4px;
    border: 1px solid #2F51B5;
    border-radius: 7px;
    display: flex;
    align-items: center;
    height: fit-content;
    cursor: pointer;
}
.dashboardButton {
    font-size: 12px;
    color: #2F51B5;
    /* font-weight: 500; */
}
.flex{
    display: flex;
    gap: 0px;
    justify-content: center;
    align-items: center;
}