.ck.ck-reset.ck-editor.ck-rounded-corners{
    width: 100% !important;
}
.ck.ck-editor__top.ck-reset_all{
    width: 100% !important;
}
.ck.ck-sticky-panel{
    width: 100% !important;
}
.ck.ck-sticky-panel__content{
    width: 100% !important;
}
.ck.ck-toolbar{
    width: 100% !important;
    padding: 0px !important;
    background-color: hsla(0, 0%, 50%, .299) !important;
}
.ck.ck-toolbar__items{
    width: 100% !important;
    max-width: 100% !important;
    height: 30px !important;
    background-color: hsla(0, 0%, 50%, .299) !important;
    align-items: center !important;
    display: flex !important;
    flex-flow: row nowrap !important;
    flex-grow: 0 !important;
}
.ck.ck-toolbar__items .ck.ck-dropdown.ck-heading-dropdown{
    width: 85px !important;
}

.ck.ck-dropdown .ck-button.ck-dropdown__button {
    width: 100% !important;
    padding: 0px !important;
}

/* dropdown wrapper */
/* div */
.ck.ck-dropdown .ck-dropdown__panel.ck-dropdown__panel_se{
    width: 90px !important;
}
/* ul */
.ck-rounded-corners .ck.ck-dropdown>.ck-dropdown__panel>.ck-list{
    width: 90px !important;
}
/* button */
.ck.ck-list__item button{
    width: 90px !important;
}
/* span (text) */
.ck.ck-list__item button span{
    width: 90px !important;
    font-size: 14px !important;
}


/* main section */

.ck.ck-editor__main{
    width: 100% !important;
    height: 130px !important;
}

.ck-rounded-corners .ck.ck-editor__main>.ck-editor__editable, .ck.ck-editor__main>.ck-editor__editable.ck-rounded-corner{
    width: 100% !important;
    box-sizing: border-box !important;
    padding: 0px 4px 3px 4px !important;
    height: 100% !important;
    max-height: 115px !important;
    overflow: auto !important;
    background-color: hsla(0, 0%, 50%, 0) !important;
}


/* quill editor css */
.editorContentParent{
    height: 100%;
    width: 100%;
}
.editorContentParent .quill  {
    height: 100% !important;
    width: 100% !important;
}
.ql-snow .ql-toolbar button, .ql-snow.ql-toolbar button {
    height: 26px !important;
    padding: 4px 4px !important;
    width: 25px !important;
}
.ql-toolbar.ql-snow {
    padding: 5px 1px !important;
}
.ql-toolbar.ql-snow .ql-formats {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center !important;
    flex-wrap: wrap !important;
    padding: 0px !important;
    margin: 0px !important;
    width: 100% !important;
}
.ql-snow .ql-picker.ql-header {
    width: 90px !important;
}
.ql-toolbar.ql-snow+.ql-container.ql-snow {
    border-top: 0 !important;
    height: 77% ;
}

.ql-snow .ql-editor h1, .ql-snow .ql-editor h2, .ql-snow .ql-editor h3, .ql-snow .ql-editor h4, .ql-snow .ql-editor h5, .ql-snow .ql-editor h6{
    line-height: normal;
}

