@font-face 
{
  font-family: "FuturaBkBook";
  src: url("../fonts/FuturaBkBook.woff2") format("woff2"), 
  url("../fonts/FuturaBkBook.woff") format("woff"), 
  url('../fonts/FuturaBkBook.ttf')  format('truetype'), 
  url('../fonts/FuturaBkBook.otf')  format('OpenType'), 
  url('../fonts/FuturaBkBook.eot')  format('embedded-opentype');
}
.color{
    color: #2F51B5; 
}
.bgcolor{
    background-color: #2F51B5;
    color: white;
}
.bgcolorResult{
    /* background-color: #c1cbe7bf; */
    background-color: #0565ff1a;
    color: black;
}
.bgcolorGrey{
    background-color: hsla(0, 0%, 50%, .299);
}
.fontSize10{
    font-size: 10px;
}
.fontSize12{
    font-size: 12px;
}
.parent{
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 120%;
    padding: 8px 0px;
    background: #FFFFFF;
    /* border-radius: 20px 0px 0 20px;  */
    box-shadow: 0px 0px 5px #858282;
    z-index: 111;
    display: none;
}
.ocrParentContainer{
    width: 100%;
    height: 100%;
}
.ocrTop{
    border-bottom: 1px solid hsla(0, 0%, 50%, 0.299);
    width: 100%;
    height: 10%;
}
.ocrTopInner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 15px;
    padding: 1px 12px;
    height: 100%;
}
.ocrTopInner span{
    cursor: default;
}
.ocrTopSection{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
}

/* mid */
.ocrMid{
    width: 100%;
    height: 70%;
}
.ocrMidInner{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    font-size: 15px;
    padding: 5px 12px;
    width: 100%;
    height: 100%;
  /* border: 1px solid #ccc; */
  box-sizing: border-box;
}
.ocrMidInnerSection{
    width: 80%;
    height: 50%;
}
.ocrMidInnerSectionPart{
    border-bottom: 1px solid hsl(0deg 0% 50% / 58%);
    width: 100%;
    text-align: center;
}
.ocrMidInnerSection1{
    /* max-height: 50%;
    overflow: auto; */
    height: 50%;
    width: 93%;
    margin-top: 5px;
}
.ocrCapturedImage{
    width: 95%;
    height: 95%;
    object-fit: contain;
    background-repeat: no-repeat;
    padding: 10px 0px;
    box-sizing: border-box
}
.horizontalLine{
    margin: 15px 0px 2px;
    width: 100%;
    box-shadow: 0px 0px 1px hsla(0, 0%, 50%, .299);
}
.ocrText{
    /* padding: 0px 0px 10px 0px; */
    font-size: larger;
}
.ocrResulContainer{
    background-color: hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    box-sizing: border-box;
    /* height: 100%; */
    height: 108%;
    width: 100%;
    margin-top: 6px;
    padding-bottom: 3px;
}
.ocrResulText{
    box-sizing: border-box;
    font-size: 12px;
    height: 83%;
    overflow: auto;
    padding: 10px;
    width: 100%;
    word-wrap: break-word;
    font-weight: 100;
    font-family: sans-serif;
    color: black;
    line-height: 15px;
}
.ocrResulEvents{
    align-items: center;
    /* background-color: hsla(0, 0%, 50%, .299); */
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding: 3px 8px 0;
    width: 100%;
}
.ocrResulEvents div{
    cursor: pointer;
    font-size: 12px;
    font-weight: 100;
    font-family: sans-serif;
    color: black;
    line-height: 15px;
}
.ocrResulEventsChild{
    display: flex;
    gap: 7px;
}

.ocrFooter{
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding: 10px 21px;
    height: 10%;
    margin-top: 1px;
    box-sizing: border-box;
    margin-bottom: 5px;
}
.ocrFooterWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.ocrFooterInner{
    display: flex;
    gap: 15px;
}
.ocrFooterInner div{
    background-color: hsla(0, 0%, 50%, .299);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 5px;
    cursor: pointer;
}
.ocrFooterInner div:hover{
    background-color: #2F51B5;
}
.ocrFooterInner img{
    width: 20px;
    object-fit: contain;
}
.ocrFooterInner2 div{
    background-color: hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 5px;
    cursor: pointer;
}
.ocrFooterInner2 div:hover {
    background-color: #2F51B5;
    color: white;
}
.cursorPointer{
    cursor: pointer;
}


/*-------------------------------- events logics -----------------------------------------*/

.copyMessage{
    padding: 0px 8px 2px;
    margin: 0px;
    font-size: 12px;
    color: #2F51B5;
    font-weight: bolder;
}
.flex{
    display: flex;
    justify-content: center;
    align-items: center;
}
.selectedText{
    /* background-color: #2F51B5;
    color: white; */
}

.editButtons{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.editCancel{
    border-radius: 7px !important;
    border: 1px solid hsla(0, 0%, 50%, .299);
    width: 20%;
    /* font-weight: 500; */
    font-size: 12px;
}
.editSave{
    background-color: #2F51B5 !important;
    border-radius: 7px !important;
    color: #fff !important;
    width: 20%;
    font-weight: 500;
    font-size: 14px;
}
.editEventChilds{
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* gap: 5px; */
    width: 100%;
    box-sizing: border-box;
    padding: 1px 0px 1px;
    border-bottom: 1px solid hsla(0, 0%, 50%, .299);

}

.closeIcon{
    width: 15px;
    padding-right: 6px;
    cursor: pointer;
}
.ocrEventMid{
    width: 100%;
    height: 80%;
}
.ocrEventSearchMidInnerSection{
    height: 20%;
    width: 93%;
    padding: 8px 4px 0px;
}
.ocrEventSearchMidInnerSection2{
    height: 60%;
    width: 93%;
}
.ocrEventMidText{
    font-size: medium;
    font-weight: bolder;
}
.ocrEventInputWrapper{
    margin: 10px 0px;
}
.ocrEventInput{
    width: 90%;
    border: 1px solid hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    font-size: 18px;
    padding: 7px 12px;
}
.ocrEventResultText{
    height: 100%;
}

/* ------------------------------- translate event page ---------------------------- */
.ocrEventTranslateMidInnerSection{
    height: 40%;
    width: 95%;
    padding: 8px 4px 0px;
}
.ocrTranslateEventText1{
    height: 70%;
    border: 1px solid hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    margin-top: 5px;
}
.TranslateLanguageWrapper{
    display: flex;
    gap: 7px;
    align-items: center;
    justify-content: flex-start;
    height: 25px;
    cursor: pointer;
}
.ocrEventTranslateMidInnerSection2{
    height: 50%;
    padding: 8px 4px 0;
    width: 95%;
    box-sizing: border-box;

}
.ocrTranslateEventText2{
    height: 100%;
}
.ocrTranslateEventText2inner{
    height: 80%;
}
.ocrResultTranslateEventsChild{
    display: flex;
    gap: 7px;
    justify-content: flex-end;
    padding: 8px 10px 0px;
}
.ocrResultTranslateEventsChildInner{
    display: flex;
    gap: 7px;
    justify-content: center;
    align-items: baseline;
}
.ocrResultTranslateEventsChild img{
    cursor: pointer;
    width: 18px;
}
.dropdownButton{
    position: relative;
}
.countryDropdownList{
    width: 90%;
    height: 100px;
    background-color: white;
    border: 1px solid hsla(0,0%,50%,.299);
    border-radius: 7px;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
    gap: 3px;
    overflow: auto;
}

.disableTranslationDropDown{
    pointer-events: none;
}
.countryDropdownList::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  .countryDropdownList::-webkit-scrollbar-track {
    background: #f1f1f1; 
  }
   
  /* Handle */
  .countryDropdownList::-webkit-scrollbar-thumb {
    background: #007bff; 
    border-radius: 7px;
  }
  
  /* Handle on hover */
  .countryDropdownList::-webkit-scrollbar-thumb:hover {
    background: #0553a6; 
  }
.dropdownItem{
    margin: 4px 0px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 7px;
    padding: 0px 4px;
}
/* ------------------------------- text to spech event page ---------------------------- */
.ocrseachBoxHeight{
    height: 100%;
}

.ocrTTSMidInner{
    height: 110%;
    max-height: 110%;
}
.ocrEventTTSMidInnerSection{
    height: 85%;
    max-height: 85%;
    width: 95%;
    padding: 8px 4px 0px;
    box-sizing: border-box;
}
.ocrEventTTSMidInnerSection2{
    box-sizing: border-box;
    height: 15%;
    width: 95%;
    padding: 8px 4px 0px;
}
.textToSpeechSection2inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.textToSpeechSection2inner img{
    cursor: pointer;
}
.ocrTTSEventTextWrapper{
    height: 88%;
    border: 1px solid hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    margin-top: 5px;
}
.flexTTS{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 6px;
}
.speedDropdownWrapper{
    position: relative;
    cursor: pointer;
}
.speedDropdown{
    background-color: hsla(0, 0%, 5%, 0.299);
    border-radius: 7px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    gap: 1px;
    left: -45px;
    padding: 2px 4px;
    position: absolute;
    top: -25px;
    width: 35px;
}
.disabled {
    pointer-events: none;
    opacity: 0.5;
}
/* background: [background-color] [background-image] [background-position] / [background-size] [background-repeat] [background-origin] [background-clip] [background-attachment]; */
.imagePlay{
    background-image: url('../images/ocr-edit-icons/ocr-play-blue.png') ;
    background-size: 18px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: white;
    border-radius: 3px;
    height: 25px;
    width: 25px;
    cursor: pointer;
}
.imagePlay:hover{
    background-image: url('../images/ocr-edit-icons/ocr-play-white.png') ;
    background-color: #2F51B5;
}
.imagePause{
    background-image: url('../images/ocr-edit-icons/ocr-pause-blue.png') ;
    background-size: 18px;
    background-position: center;
    background-repeat: no-repeat;
    background-color: white;
    border-radius: 3px;
    height: 25px;
    width: 25px;
    cursor: pointer;
}
.imagePause:hover{
    background-image: url('../images/ocr-edit-icons/ocr-pause-white.png') ;
    background-color: #2F51B5;
}
.imageWidth{
    width: 20px;
}
/* -------------------------------interrogation event page ---------------------------- */

.ocrEventQuestionMid{
    height: 100%;
}
.ocrEventQuestionMidInnerSection{
    /* background-color: hsla(0, 0%, 50%, .299); */
    height: 375px;
    max-height: 375px;
    overflow: auto;
    padding: 8px 7px 0;
    width: 95%;
    /* border-radius: 7px; */
}

.emptyText{
    font-size: large;
    text-align: -webkit-center;
    color: #2F51B5;
    border: 1px solid hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    background-color: hsla(0, 0%, 50%, .299);
    margin-top: 50px;
}
.responseText{
    width: 90%;
    padding: 5px 7px;
    font-size: 12px;
    border-radius: 7px;
    float: left;
    /* font-weight: lighter; */
    font-family: "FuturaBkBook";
    margin: 3px 0px;
    border: 1px solid #0565ff;
}
.ocrEventQuestionMidInnerSection2{
    height: 40px;
    max-height: 45px;
    /* overflow: hidden; */
    width: 95%;
    padding: 8px 4px 0px;
    position: relative;
    /* bottom: -80px; */
}
.questionText{
    width: 90%;
    padding: 5px 7px;
    font-size: 12px;
    border-radius: 7px;
    float: left;
    /* font-weight: lighter; */
    font-family: "FuturaBkBook";
    background-color: hsla(0, 0%, 50%, .299);
    margin: 3px 0px
}
.timestamp{
    font-size: 9px;
    float: left;
    margin: 2px 0px 10px;
}
.restimestamp{
    font-size: 9px;
    float: right;
    margin: 2px 0px 10px;
}


.inputWrapper {
    position: relative;
    display: inline-block;
    width: 100%; 
  }
  
  .ocrEventQuestionInput {
    width: 100%; /* Adjust the width as needed */
    border: 1px solid hsla(0, 0%, 50%, .299);
    border-radius: 7px;
    /* font-size: 18px; */
    padding: 8px 40px 8px 12px; /* Adjust padding-right for the clickable area */
    box-sizing: border-box; /* Ensures padding and border are included in the width */
  }
  
  .clickableIcon {
    position: absolute;
    right: 10px; /* Adjust the position as needed */
    top: 50%;
    transform: translateY(-50%);
    width: 20px; /* Adjust the size as needed */
    height: 20px; /* Adjust the size as needed */
    background-image: url('../images/ocr-edit-icons/ocr-send.png'); /* Specify the path to your image */
    background-size: cover; /* Adjust as needed */
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Ensure the image doesn't repeat */
    cursor: pointer; /* Change cursor to pointer to indicate it's clickable */
  }

 
  .height112{
    height: 112%;
  }