.parent{
    box-sizing: border-box;
    /* background-color: #c7c2c22e; */
    background-color: hsla(0,0%,50%,.151);;
    /* float: none; */
    width: 95%;
    margin: 5px auto;
    display: flex;
    flex-direction: column;
}
.top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 2%;
    cursor: default;
}
.modeNameOuter {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    font-size: 18px;
}
.modeName{
    cursor: default;
    display: inline-block;
    font-size: 18px;
    text-align: center;
}
.bgcolor {
    background-color: #2F51B5;
}
.color{
    color: #2F51B5;
}
.buttonCapture{
    width: 35%;
    /* height: fit-content; */
    padding: 6px 4px;
    margin: 8px 4px;
    border: transparent;
    border-radius: 7px;
    color: white;
    cursor: pointer;
}
.buttonRecorder{
    width: 35%;
    /* height: fit-content; */
    padding: 6px 4px;
    margin: 8px 4px;
    border: transparent;
    border-radius: 7px;
    color: white;
    cursor: pointer;
}

.delayParent{
    width: fit-content !important;
}
.pointer{
    cursor: pointer;
}
.flex{
    display: flex;
    align-items: center;
    justify-content: center;
}
