.parent{
    background-color: #fff;
    border-bottom-left-radius: 6%;
    border-top-left-radius: 6%;
    box-shadow: 0 3px 7px #000;
    display: block;
    height: 50%;
    position: inherit;
    right: 0;
    width: 100%;
    z-index: 999;
    height: 100%;
}
.cursor{
    cursor: pointer;
}